.output {
  display: block !important;
  display: none;
  background-color: var(--color-output-bg);

  &.visible {
    display: block;
  }

  &-container {
    // display: none !important;
  }

  & .invest {
    display: none;
  }

  .main-min-width-750px & {
    background-color: transparent;

    &-container  {
      display: flex;
      column-gap: 30px;
      padding-top: 3px;

      & > * {
        width: 50%;
      }
    }

    &-bottom {
      padding: 16px 20px 14px 20px;
      background-color: var(--color-output-bg);
    }

    &-item {
      min-height: 94px;
      padding-top: 18px;
      padding-bottom: 18px;

      &::after {
        right: -30px;
      }
      &::before {
        right: -30px;
        height: calc(100% - 24px);
      }

      &.--available {

        &::after {
          top: 41%;
        }

        &::before {
          top: 44%;
        }
      }

      &.--in-processing {

        &::before {
          transform: translate(50%, 26%);
        }
      }

      &.--derived {

        &::after {
          top: 56%;
        }
      }
    }

    &-button {
      display: none;
    }

    &__form {
      padding: 20px 20px 23px 20px;
    }

    & .invest {
      display: block;
    }
  }


  &__form {
    padding: 15px 15px 23px 15px;
    border-radius: 14px;
    background-image: linear-gradient(to right top, #2E2482, #4420E2);

    & > *:not(:last-child) {
      margin-bottom: 10px;
    }

    &-bottom {
      display: flex;
      align-items: center;
      column-gap: 10px;
      margin-top: 20px;
      padding-right: 5px;
    }

    &-button {
      animation: scale infinite 1.5s;
      @keyframes scale {
        50% {
          transform: scale(1.05);
        }
      }
    }
  }

  &-text {
    text-align: right;
    font-size: 12rem;
    color: var(--color-light);
  }

  &-bottom {
    padding: 0 15px;
    border-radius: 14px;

    &__title {
      font-weight: 500;
      font-size: 12rem;
      color: var(--color-center-subtitle);
      transform: translateY(5px);
    }
  }

  &__list {
    padding-right: 70px;
  }

  &-modal {
    display: grid;
    grid-template-columns: 50% 50%;
    column-gap: 10px;
    max-width: 675px;
    // margin-top: -46px;
    padding: 15px;
    border-radius: 14px;
    background-color: var(--color-center-block-bg-invest);

    & > * {
      grid-column: 1/-1;
    }

    & .--span-1 {
      grid-column: auto;
    }

    &__time {
      display: inline-flex;
      align-items: center;
      min-height: 28px;
      width: min-content;
      margin-bottom: 109px;
      padding: 5px 10px;
      padding-left: 28px;
      font-size: 14rem;
      font-weight: 900;
      border-radius: 7px;
      color: var(--color-light);
      background: var(--color-blue-100) url(../img/time.svg) left 8px center no-repeat;
    }

    &__title {
      margin-bottom: 13px;
      letter-spacing: -0.8px;
    }

    &__label {

      &-select {
      margin-bottom: 8px;
      }
      &-input {
      margin-bottom: 10px;
      }
    }

    &__icon {
      display: none;
    }

    &__list {
      margin: 0;
      margin-bottom: 40px;
      padding: 0 0 0 11px;
      list-style: none;
      color: var(--color-center-subtitle);
    }

    &__item {

      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }

    &__descr {
      font-size: 12rem;

      & span {
        position: relative;

        &::before {
          position: absolute;
          content: "";
          display: inline-block;
          top: 50%;
          transform: translateY(-50%);
          left: -10px;
          // top: 6.9px;
          vertical-align: middle;
          width: 4px;
          height: 4px;
          border-radius: 100%;
          background-color: currentColor;
        }
      }
    }

    &__button {
      padding: 10px 15px;
    }

    @media (min-width: 675px) {

      grid-template-columns: 1fr 50px 1fr;
      column-gap: normal;
      padding: 30px;
      // transform: translate(212px, 296px);

      &__time {
        grid-column: 3/-1;
        justify-self: end;
        height: fit-content;
        margin-bottom: 0;
      }

      &__title {
        grid-column: 1/2;
        grid-row: 1;
        margin-bottom: 23px;
        font-size: 20rem;
        letter-spacing: normal;
      }

      &__label {

        &-select {
        // margin-bottom: 8px;
        }
        &-input {
          margin-bottom: 12px;

          &.--row-start {
            grid-column: 1/2;
            grid-row: 3;
          }
          &.--row-end {
            grid-column: 3/-1;
            grid-row: 3;
          }
        }
      }

      &__icon {
        display: block;
        grid-column: 2;
        grid-row: 3;
        align-self: center;
        justify-self: center;
        margin-top: 14px;
      }

      &__list {
        margin-bottom: 18px;
      }

      &__descr {
        line-height: 15px;
      }

      &__item {

        &:not(:last-child) {
          margin-bottom: 9px;
        }
      }

      &__button {

        background-image: url(../img/arrow-right.svg);
        background-repeat: no-repeat;
        background-position: right 23px center;

        &.--row-end {
          grid-column: 3/-1;
          // grid-row: 1;
        }
      }
    }
  }
}

.graph-modal__container {
  position: relative;
  width: auto;
  margin: 50px auto;
  display: inline-block;
  vertical-align: middle;
  background-color: transparent;
  border-radius: 0;
  padding: 0;
  cursor: default;
  text-align: left;
  display: none;
}

.graph-modal__close {

  .main-min-width-750px & {
    display: none;
  }
}

.disable-scroll > *:not(.graph-modal) {
  animation: modal 0.3s forwards linear;
}

@keyframes modal {
  0% {
    filter: none;
  }
  100% {
    filter: blur(5px);
  }
}
