// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "ObjectSans", "Open Sans", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // vars
  --input-border: 1px solid var(--color-input-border);

  // colors
  --color-light: #fff;
  --color-light-blue: #F4F7FE;
  --color-dark-deep-blue-100: #151A38;
  --color-dark-deep-blue-50: #1A2040;
  --color-deep-blue-100: #2B3674;
  --color-deep-blue-50: #707EAE;
  --color-deep-blue-25: #A3AED0;
  --color-blue-100: #2E2482;
  --color-blue-25: #E2E1FE;
  --color-green-100: #24B389;
  --color-black-25: #F4F4F5;
  --color-black-50: #5F5F5F;
  --color-input-border: #E9EAEC;
  --color-gradient-silver: linear-gradient(205deg, #BECEDB, #596065, #FEFFFF, #5B6267);
  --color-profile-icon: var(--color-deep-blue-25);
  --color-profile-icon--border: var(--color-green-100);
  --color-profile-title: var(--color-deep-blue-100);
  --color-nav-item-shadow: var(--color-green-100);
  --color-nav-item-icon-active: var(--color-green-100);
  --color-nav-item-hover: var(--color-deep-blue-50);
  --color-nav-border-v: #ebe8ff;
  --color-nav-border-h: #e2e1fe;
  --color-border-dark: #0F132C;
  --color-nav-link-active: var(--color-green-100);
  --color-nav-link: var(--color-deep-blue-25);
  --color-nav-link-inside: var(--color-light-blue);
  --color-center-title: var(--color-blue-100);
  --color-text-light-dark: var(--color-light-blue);
  --color-text-deep-50: var(--color-deep-blue-50);
  --shadow-center-header: rgb(0%, 0%, 0%, 0.25);
  --shadow-center-block: 0 18px 40px 0 rgb(44%, 56%, 69%, 0.12);
  --shadow-center-block-table: 0 0 40px 0 rgb(44%, 56%, 69%, 0.12);
  --shadow-center-block-active: 0 18px 40px 0 rgb(44%, 56%, 69%, 0.12),
                                0 0 5px 0 rgb(0%, 0%, 0%, 0.1),
                                0 4px 20px 0 rgb(0%, 0%, 0%, 0.15);
  --color-button-danger: #F47A58;
  --color-button-danger-hover: #f25327;
  --color-main-bg: var(--color-light);
  --color-center-block-bg: var(--color-light);
  --color-center-block-bg-invest: var(--color-light-blue);
  --color-center-subtitle: var(--color-deep-blue-100);
  --color-center-bottom-text: var(--color-deep-blue-100);
  --color-center__form-input-button: var(--color-light);
  --color-input-bg: var(--color-light);
  --color-aside-card-time: var(--color-deep-blue-50);
  --color-auth: var(--color-blue-100);
  --color-center__header-button--bell: var(--color-deep-blue-25);
  --center-form-margin-top: -16px;
  --color-item-border: rgba(67, 24, 255, 0.1);
  --color-personal-text-big: var(--color-deep-blue-100);
  --color-header-link-border: var(--color-nav-border-h);
  --color-output-item-border: #e2e1fe;
  --color-output-bg: var(--color-light-blue);
  --color-button-payment: var(--color-blue-25);
  --color-button-fade: #AAABAE;
  --color-button-fade-border: var(--color-input-border);

  &[data-theme-dark] {
    --color-profile-title: var(--color-deep-blue-25);
    --color-nav-border-v: #0F132C;
    --color-nav-link: var(--color-deep-blue-50);
    --color-nav-link-inside: var(--color-dark-deep-blue-100);
    --color-nav-icon-active: var(--color-gradient-silver);
    --color-nav-item-hover: var(--color-blue-100);
    --color-center-title: var(--color-light);
    --color-center-subtitle: var(--color-light);
    --color-main-bg: var(--color-dark-deep-blue-100);
    --color-center-block-bg: var(--color-dark-deep-blue-50);
    --color-center-block-bg-invest: var(--color-dark-deep-blue-50);
    --color-input-bg: var(--color-dark-deep-blue-100);
    --center-subtitle: var(--color-light);
    --color-center-bottom-text: var(--color-deep-blue-25);
    --color-center__form-input-button: var(--color-dark-deep-blue-50);
    --color-aside-card-time: var(--color-deep-blue-25);
    --color-auth: var(--color-green-100);
    --color-center__header-button--bell: var(--color-light);
    --color-item-border: #0F132C;
    --color-personal-text-big: var(--color-light);
    --color-header-link-border: var(--color-deep-blue-100);
    --color-output-item-border: #0F132C;
    --color-output-bg: var(--color-dark-deep-blue-100);
    --color-button-payment: var(--color-dark-deep-blue-50);
    --color-button-fade: var(--color-deep-blue-50);
    --color-button-fade-border: var(--color-border-dark);
  }
}
