.center__form {
  margin-top: var(--center-form-margin-top);
  padding: 19px 20px 25px;
  border-radius: 14px;
  background-color: var(--color-center-block-bg);
  box-shadow: var(--shadow-center-block);

  &-top {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 22px;
  }

  &-list {
    display: flex;
    margin-right: -5px;
  }

  &-link {
    padding: 7px 20px;
    border-radius: 2em;
    font-size: 12rem;
    white-space: nowrap;
    color: var(--color-deep-blue-50);

    &.--active {
      color: var(--color-light);
      background-color: var(--color-blue-100);
    }

    @include hover {
      color: var(--color-light);
      background-color: var(--color-blue-100);
    }
  }

  &-inner {
    display: none;
    --inner-gap: 30px;
    gap: var(--inner-gap);

    &.--active {
      display: flex;
    }

    &>* {
      width: calc(50% - (var(--inner-gap) / 2));
      display: flex;
      flex-direction: column;
      gap: 15px;
    }

    &-left .inner {
      display: flex;
      gap: 14px;
    }
  }

  &-input {

    &-tel {
      max-width: 330px;
      font-size: 16rem;
    }

    &-button {
      position: absolute;
      right: 11px;
      bottom: 11px;
      padding: 7.5px 15px;
      border-radius: 14px;
      font-size: 12rem;
      color: var(--color-center__form-input-button);
      background-color: var(--color-green-100);
    }
  }

  &-label-file {
    position: relative;
    display: flex;
    align-items: center;
    height: 21px;
    padding-left: 29px;
    cursor: pointer;
    font-size: 14rem;
    white-space: nowrap;
    color: var(--color-center-title);

    @media (any-hover: hover) {
      &:hover {
        [data-theme-dark] & {
          color: var(--color-center-bottom-text);

          & svg {
            fill: var(--color-center-bottom-text);
          }
        }
      }
    }



    & svg {
      position: absolute;
      width: 21px;
      height: 21px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      fill: var(--color-blue-100);

      [data-theme-dark] & {
        fill: var(--color-light);
      }
    }
  }

  & .just-validate-error-label {
    margin-top: 5px;
  }
}

.upload-zone {
  display: flex;
  align-items: center;
  min-height: 70px;
  margin-bottom: 25px;
  padding: 15px 25px 15px 20px;
  border: 1px solid var(--color-input-border);
  border-radius: 7px;
  background-color: var(--color-input-bg);
  font-size: 14rem;
  color: var(--color-deep-blue-50);

  [data-theme-dark] & {
    border: none;
  }

  &._active {
    background-color: var(--color-input-border);
  }

  &_dragover {
    position: relative;
    height: 100%;
    width: 100%;

    & p {
      height: 100%;
      line-height: 132%;

      @media (any-hover: hover) {
        &:hover {
          [data-theme-dark] & {
            color: var(--color-center-bottom-text);
          }
        }
      }

      &#uploadForm_Status {
        position: absolute;
        top: 100%;
        color: var(--color-button-danger);
      }
    }
  }
}

#uploadForm_Status-list {
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-top: -10px;
  margin-bottom: 15px;

  & p {

  }
}

#uploadForm_Status {
  margin-top: -10px;
  margin-bottom: 15px;
}


@media (max-width: 1264px) {

  .upload-zone {
    flex-direction: column;
    align-items: flex-start;

    &_dragover {
      margin-bottom: 10px;
    }
  }
}

@media (max-width: 768px) {

  .center__form {

    &-inner {
      flex-direction: column;

      &>* {
        width: 100%;
      }
    }
  }
}

@media (max-width: 627px) {

  .center__form {
    --center-form-margin-top: -8px;
    padding: 17px 15px 25px;

    &-link {
      padding: 5px 11px;
    }

    &-top {
      margin-bottom: 19px;
    }

    &-inner {
      --inner-gap: 10px;

    & > * {
        gap: 10px;
      }
    }
  }

  .upload-zone {

    margin-bottom: 30px;

    &_dragover {
      margin-bottom: 13px;
    }
  }
}
