.choices {
  height: 50px;
  margin-top: 7px;
  padding: 0 20px 0 0;
  // border: 1px solid var(--color-input-border);
  // border-radius: 14px;
  // background-color: var(--color-input-bg);

  &.is-focused {
    outline: 3px solid var(--color-deep-blue-50) !important;
  }
}

.choices {

  .choices.input &__inner {
    display: flex;
    align-items: center;
    border: none;
    width: 100%;
    height: 100%;
    padding-left: 0;
    padding-top: 0;
    padding-bottom: 0;
    background-color: transparent;
  }

  &__list--single {
    height: 100%;
    padding-left: 0;
    padding-top: 0;
    padding-bottom: 0;
  }

  .is-open &__list--dropdown,
  .is-open &__list[aria-expanded] {
    border: 1px solid var(--color-input-border);
    border-radius: 14px;
    // border-color: var(--color-input-bg);
    border-color: var(--color-input-border);
    left: 0;
  }

  [data-theme-dark] .is-open &__list--dropdown,
  [data-theme-dark] .is-open &__list[aria-expanded] {
    border-color: var(--color-deep-blue-50);
  }

  [data-theme-dark] &__list--dropdown {
    background-color: var(--color-input-bg);
    border-color: var(--color-deep-blue-50);
  }


  &__list--dropdown .choices__list,
  &__list[aria-expanded] .choices__list {
    outline: none;
    border: none;
  }

  &__list--single .choices__item,
  .choices__list--dropdown .choices__item {
    display: flex;
    align-items: center;
    height: 100%;
    padding-left: 10px;
    color: var(--color-deep-blue-50);

    @media (any-hover: hover) {
      [data-theme-dark] .choices:hover & {
        color: var(--color-dark-deep-blue-100);
      }
    }
  }

  &.--bg .choices__list--single .choices__item,
  &.--bg .choices__list--dropdown .choices__item {
    padding-left: 55px;
    background-repeat: no-repeat;
    background-position: 0 center;
  }

  &[data-type*=select-one]::after {
    content: "";
    height: 6px;
    width: 11px;
    border-style: none;
    border-color: transparent;
    border-width: none;
    position: absolute;
    right: 11.5px;
    top: 50%;
    margin-top: -2.5px;
    pointer-events: none;
    background: url(../img/input-arrow.svg);
    transition: transform .3s linear;
  }

  &[data-type*=select-one].is-open::after {
    margin-top: -2.5px;
    // transform: rotate(180deg);
    // transform: rotate(180deg);
    transform: scale(1, -1);
  }

  &__list--dropdown .choices__item--selectable.is-highlighted,
  &__list[aria-expanded] .choices__item--selectable.is-highlighted {
    [data-theme-dark] & {
      background-color: var(--color-deep-blue-50);
      color: var(--color-dark-deep-blue-100);
    }
  }

  &__list--dropdown .choices__item--selectable,
  &__list[aria-expanded] .choices__item--selectable {
    [data-theme-dark] & {
      background-color: var(--color-dark-deep-blue-100);
      color: var(--color-deep-blue-50);
    }
  }
}

@media (any-hover: hover) {

  [data-theme-dark] .choices[data-type*=select-one]:hover::after {
    background: url(../img/input-arrow-dark.svg);
  }
}
