.center__vallet {
  display: none;

  &.--active {
    display: block;
  }

  &-subtitle {
    max-width: 389px;
    margin-top: -8px;
  }

  &-label:not(:last-child) {
    margin-bottom: 20px;
  }

  &-input {
    padding-left: 55px;
    padding-right: 45px;
    background-repeat: no-repeat;
    background-position: 6px 50%;

    &.--USDT {
      background-image: url(../img/coin1.svg);
    }
    &.--Ethereum {
      background-image: url(../img/ethereum.svg);
    }
    &.--BEP20 {
      background-image: url(../img/bep20.svg);
    }

    &-btn {
      --size: 18px;
      width: var(--size);
      height: var(--size);
      top: 50%;
      right: 15px;
      transform: translateY(-50%);

    }
  }

  // &-btn {
  //   margin-top: 25px;
  //   padding-left: 55px;
  //   background-image: url(../img/cross.svg);
  //   background-repeat: no-repeat;
  //   background-position: 23px 50%;
  // }
}
