.nav {
  grid-area: nav;
  border-right: 1px solid var(--color-nav-border-v);


  &__list {
    display: flex;
    flex-direction: column;
    // flex-wrap: wrap;
    height: 100%;
  }

  &__item {
    position: relative;
    display: block;
    border-bottom: 1px solid var(--color-nav-border-v);
    background-color: var(--color-main-bg);

    &-inner {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding-top: 25px;
      padding-bottom: 16.5px;
      border-top-right-radius: 14px;
      border-bottom-right-radius: 14px;
      background-color: var(--color-main-bg);
    }

    & svg {
      width: 26px;
      height: 26px;
      margin-bottom: 9px;
      fill: var(--color-nav-link);
    }

    @media (any-hover: hover) {

      &:hover {

        &:not(.nav__item--active) {

          & .--color-nav-link--active {
            fill: var(--color-nav-item-hover);
          }

          & .stroke {
            stroke: var(--color-nav-item-hover);
          }

          & svg {
            fill: var(--color-nav-item-hover);
          }

          & .nav__link {
            color: var(--color-nav-item-hover);
          }

          :root[data-theme-dark] & {
            & .--color-nav-link--active {
              fill: url(#g);
            }

            & .stroke {
              stroke: url(#g);
            }

            & svg {
              fill: url(#g);
            }

            & .nav__link {
              color: var(--color-light);
            }
          }
        }
      }
    }


    &--active:not(.--exit) {

      position: relative;
      z-index: 1;

      & .--color-nav-link--active {
        fill: var(--color-nav-link-active);
      }

      & .stroke {
        stroke: var(--color-nav-link-active);

        [data-theme-dark] & {
          stroke: url(#g);
        }
      }

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        z-index: -2;
        transform: translateY(-50%);
        width: 65%;
        height: 90%;
        box-shadow: 0 0px 34px -13px var(--color-nav-item-shadow),
          0 0px 34px -120px var(--color-nav-item-shadow);
      }

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: 5px;
        background-image: url(../img/nav-rect-active.svg);
        background-size: auto 100%;
        height: 88%;
        font-size: 0;
      }

      & svg {
        fill: var(--color-nav-link-active);

        [data-theme-dark] & {
          fill: url(#g);
        }
      }


    & .nav__link {
      color: var(--color-nav-link-active);
    }

    --color-nav-link: var(--color-blue-100);

    :root[data-theme-dark] & {

      // --color-nav-link: var(--color-deep-blue-25);

      & .nav__item-inner {
        border-top-right-radius: 14px;
        border-bottom-right-radius: 14px;
        background-color: var(--color-blue-100);
      }

      &::before {
        box-shadow: none;
      }

      & .--color-nav-link--active {
        fill: url(#g);
      }

      & .nav__link {
        color: var(--color-light);
      }
    }
  }

  &:last-child,
  &.--exit {
    margin-top: auto;
    border-bottom: none;
    border-top: 1px solid var(--color-nav-border-v);

    & div {
      padding: 11px 0;
    }

    & svg {
      height: 15px;
    }
  }

  &.--else {
    display: none;
  }
}

&__link {
  pointer-events: none;
  font-size: 10rem;
  font-weight: 900;
  text-transform: uppercase;
  color: var(--color-nav-link);

  &::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}
}

@media (max-width: 627px) {
  .nav {
    padding: 0 15px;

    &__list {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding: 13px 0;

      &.active {
        display: grid;
        grid-template-columns: repeat(auto-fit, 70px);
        row-gap: 10px;
      }
    }

    &__item {
      display: flex;
      justify-content: center;
      height: 38px;
      border-bottom: none;

      &.visible {
        display: flex !important;
      }

      &:nth-child(n+7):not(:last-child) {
        display: none;
      }

      &:last-child {
        border: none;

        & div {
          padding: 0 0;
        }

        & svg {
          height: 21px;
        }
      }

      &-inner {
        width: 54px;
        padding-top: 0;
        padding-bottom: 0;
      }

      & svg {
        --size: 21px;
        width: var(--size);
        height: var(--size);
        margin-bottom: 3px;
      }

      &--active {

        &::before,
        &::after {
          display: none;
        }
      }

      &.--else {
        display: block;

        .active & {
          display: flex;
          justify-content: center;
          grid-column: -1/-2;
        }
      }
    }

    &__link {
      font-size: 8px;
    }
  }
}


@media (max-width: 580px) {
  .nav {

    &__item {

      &:nth-child(n+6):not(:last-child) {
        display: none;
      }
    }
  }
}

@media (max-width: 450px) {
  .nav {

    &__item {

      &:nth-child(n+5):not(:last-child) {
        display: none;
      }
    }
  }
}
