.header {

  &__top {
    grid-area: header;
    display: flex;
    align-items: center;
    // justify-content: space-between;
    padding: 18px 30px 18px;

    & .profile__img-wrap {
      display: none;
    }
  }

  &__title {
    margin-right: 15px;
    font-size: 24rem;
    font-weight: 900;
    letter-spacing: -0.018em;
    color: var(--color-center-title);
  }

  &__button {
    display: none;
    margin-left: auto;
    font-size: 14rem;
    color: var(--color-text-deep-50);

    &--bell,
    &--moon {
      display: none;
    }

    &--moon {
      fill: var(--color-nav-link);
    }

    [data-profile] & {
      display: block;
    }
  }

  &__link {
    padding: 0 7px;
    border: 1px solid var(--color-header-link-border);
    border-radius: 7px;
    font-size: 13rem;
    font-style: normal;
    line-height: 26px;
    color: var(--color-aside-card-time);
    transform: translateY(2px);

    [data-profile] & {
      display: none;
    }
  }

  &__profile-img {
    display: none;
    position: relative;
    margin-top: 5px;
  }
}

@media (max-width: 627px) {
  .header {

    &__title {
      font-size: 19rem;
    }

    &__link {
      font-size: 16rem;
    }

    &__top {
      padding: 15px 15px;
    }
    &__profile-img {
      display: block;
      width: 30px;
      height: 30px;
      margin-top: 0;

      &::after {
        position: absolute;
        content: "";
        display: block;
        bottom: 1px;
        right: 2px;
        --size: 9.84px;
        width: var(--size);
        height: var(--size);
        background-size:cover;
        background-image: url("../img/check.svg");
      }

      img {
        border: 2px solid var(--color-profile-icon--border);
        border-radius: 100%;
      }
    }

    &__button {
      display: none;

      &--bell,
      &--moon {
        display:block;
        width: 24px;
        height: 24px;
        margin-right: 21px;

        & svg {
          width: 100%;
          height: 100%;
        }
      }

      &--moon {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        fill: var(--color-nav-link);

        & svg {
          width: 17px;
          margin-top: -3px;
        }
      }
    }


