.center {
  display: none;
  grid-area: center;

  &.visible {
    display: block;
  }

  &__title {
    margin-right:auto;
    font-size: 24rem;
    font-weight: 900;
    letter-spacing: -0.018em;
    color: var(--color-center-title);
  }

  &__subtitle {
    margin-right: 20px;
    font-size: 18rem;
    color: var(--color-center-subtitle);

    @media (max-width:1337px) {
      margin-bottom: 20px;
    }

    &--bold {
      line-height: 2;
      font-weight: 700;
    }
  }

  &__header {

    &-top {
      grid-area: header;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 18px 30px 18px;

      & .profile__img-wrap {
        display: none;
      }
    }

    &-bottom {
      display: flex;
      align-items: center;
      padding: 3.1% 3.4% calc(3.1% - var(--center-form-margin-top)) 2.5%;
      border-top-left-radius: 14px;
      border-top-right-radius: 14px;
      background-color: var(--color-blue-100);
      box-shadow: 0 0 4px 0 var(--shadow-center-header);

      & address {
        display: flex;
        flex-direction: column;
        margin-bottom: 4px;
      }
    }

    &-button {
      font-size: 14rem;
      color: var(--color-text-deep-50);

      &--bell,
      &--moon {
        display: none;
      }

      &--moon {
        fill: var(--color-nav-link);
      }
    }
  }

  &__profile {

    &-img-wrap {
      position: relative;
      margin-right: 20px;

      &:after {
        position: absolute;
        bottom: 6%;
        right: 6%;
        content: "";
        display: block;
        width: 24px;
        height: 24px;
        background: url("../img/check.svg") no-repeat center / cover;
      }
     }

    &-img {
      width: 128px;
      height: 128px;
      border-radius: 100%;
      border: 3px solid rgba(#09A66D, 40%);

      &-button {
        top: 6%;
        right: 6%;
        width: 24px;
        height: 24px;
      }
    }

    &-name {
      margin-bottom: 2px;
      font-size: 24rem;
      font-weight: 900;
      font-style: normal;
      color: var(--color-text-light-dark);
    }

    &-login {
      font-size: 18rem;
      font-style: normal;
      color: var(--color-text-deep-50);
    }
  }
}

