@media (max-width: 627px) {
  .center {
    // padding: 0;


    &__title {
      font-size: 19rem;
    }

    &__subtitle {
      margin-bottom: 15px;
    }

    &__header {

      &-top {
        padding: 15px 15px;

        .profile__img-wrap {
          display: block;
          width: 30px;
          height: 30px;
          margin-top: 0;
          &::after {
            bottom: 1px;
            right: 2px;
            --size: 9.84px;
            width: var(--size);
            height: var(--size);
            background-size:cover;
          }
        }
      }

      &-button {
        display: none;

        &--bell,
        &--moon {
          display:block;
          width: 24px;
          height: 24px;
          margin-right: 21px;

          & svg {
            width: 100%;
            height: 100%;
          }
        }

        &--moon {
          display: flex;
          align-items: center;
          justify-content: center;

          & svg {
            width: 17px;
            margin-top: -3px;
          }
        }
      }

      &-bottom {

        & address {
          margin-bottom: -4px;
        }
      }
    }

    &__profile {

      &-img {
        width: 86px;
        height: 86px;
      }
    }

    .profile__img {
      border: 2px solid var(--color-profile-icon--border);
      border-radius: 100%;
    }

    &__profile {

      &-name {
        font-size: 20rem;
      }

      &-login {
        font-size: 16rem;
      }
    }
  }
}
