.profile {
  grid-area: profile;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 230px;
  padding: 19px 21px 19px 21px;
  border-right: 1px solid var(--color-nav-border-v);
  border-bottom: 1px solid var(--color-nav-border-v);

  &__img-wrap {
    position: relative;
    margin-top: 5px;
    &::after {
      position: absolute;
      bottom: 4px;
      right: 4px;
      content: "";
      display: block;
      width: 18px;
      height: 18px;
      background-image: url('../img/check.svg');
    }
  }

  &__img {
    border: 2px solid var(--color-profile-icon--border);
    border-radius: 100%;
  }

  &__title {
    font-size: 14px;
    white-space: nowrap;
    color: var(--color-profile-title);
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__button {
    width: 19px;
    height: 19px;


    & svg {
      width: 100%;
      height: 100%;
      fill: var(--color-profile-icon);

      @include hover {
        fill: var(--color-deep-blue-50);
      }
    }

    &--share {
      position: relative;
      height: 20px;

      svg {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 130%;
        height: 130%;
      }
    }
  }
}


@media (max-width: 627px) {
  .profile {
    display: none;
  }
}
