.aside {
  // display: none;
  grid-area: aside;
  padding: 19px 30px;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 29px;
    padding: 0px 21px 0 10px;
  }

  &__button {
    width: 24px;
    height: 24px;

    & svg {
      width: 100%;
      height: 100%;
    }

    @include hover {

      --color-nav-link: var(--color-deep-blue-50);

      :root[data-theme-dark] & {
        --color-nav-link: var(--color-deep-blue-25);
      }
    }
  }

  &__title {
    margin-bottom: 10px;
    font-size: 14rem;
    font-weight: 900;
    color: var(--color-center-subtitle);
  }

  &__card {
    border-radius: 14px;
    padding: 15px 20px;
    box-shadow: var(--shadow-center-block);

    @include hover {
      box-shadow: var(--shadow-center-block-active);
    }

    &:not(:last-child) {
      margin-bottom: 26px;
    }

    &-title {
      margin-bottom: 10px;
      font-size: 14rem;
      font-weight: 900;
      color: var(--color-center-subtitle);
    }

    &-text {
      margin-bottom: 13px;
      font-size: 12rem;
      line-height: 18px;
      color: var(--color-center-bottom-text);
    }

    &-bottom {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }


    &-time {
      font-size: 11rem;
      color: var(--color-aside-card-time);

    }

    &-link {
      display: inline-flex;
      align-items: center;
      justify-content: space-between;
      width: 19px;
      height: 10px;
      font-size: 11rem;
      color: var(--color-aside-card-time);

      @include hover {

        --color-aside-card-time: var(--color-deep-blue-100);

        :root[data-theme-dark] & {
          --color-aside-card-time: var(--color-deep-blue-50);
        }
      }

      & svg {
        // width: 100%;
        height: 100%;
      }
    }
  }

  &__authentic {
    border-radius: 14px;
    padding: 16px 20px;
    box-shadow: var(--shadow-center-block);

    @include hover {
      box-shadow: var(--shadow-center-block-active);
    }

    & svg {
      margin-bottom: 6px;
    }

    &-title {
      margin-bottom: 8px;
      font-size: 15rem;
      font-weight: 900;
      line-height: 20px;
      color: var(--color-center-subtitle);
    }

    &-text {
      margin-bottom: 34px;
      font-size: 14rem;
      line-height: 18px;
      letter-spacing: -0.27px;
      color: var(--color-center-bottom-text);
    }

    &-button {
      padding: 12px 19px;
      border-radius: 10px;
      font-size: 14rem;
      font-weight: 700;
      color: var(--color-light);
      background-color: var(--color-green-100);
    }
  }

  &__form {

    &-title {
      margin-top: -2px;
      margin-bottom: 16px;
    }

    &-textarea {
      height: 120px;
      margin-bottom: 7px;
    }
  }
}

@media (max-width: 1159px) {
  .aside {
    // display: none;
    position: absolute;
    right: -3000px;
  }
}
