.label {
  display: flex;
  flex-direction: column;
  min-width: 0;
  // margin-bottom: 15px;
  font-size: 12rem;
  font-weight: 500;
  color: var(--color-deep-blue-25);

  &.half {
    width: 50%;
  }

  &-button {
    position: relative;
    max-width: 330px;
  }
}

.input-wrap {
  position: relative;
}

.input {
  width: 100%;
  height: 50px;
  padding: 0 20px;
  border: 1px solid var(--color-input-border);
  border-radius: 14px;
  background-color: var(--color-input-bg);
  font-size: 14rem;
  color: var(--color-deep-blue-50);
  cursor: pointer;

  @media (any-hover: hover) {
    &:hover {
      background-color: #f7f7f7;
    }

    [data-theme-dark] &:hover {
      background-color: var(--color-deep-blue-50);
      color: var(--color-dark-deep-blue-100);
    }

    [data-theme-dark] &:hover::placeholder {
      color: var(--color-dark-deep-blue-100);
    }
  }

  [data-theme-dark] & {
    border: none;
  }

  &-main {
    margin-top: 7px;
  }

  &-textarea {
    width: 100%;
    padding: 13px 20px;
    resize: none;
  }

  &.focus-visible {
    outline: 3px solid var(--color-deep-blue-50);
  }

  &::placeholder {
    font-size: 14rem;
    color: var(--color-deep-blue-50);
  }

  &-amount {
    width: 100%;
    height: 50px;
    padding: 0 50px 0 20px;
  }
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 14px;
  font-size: 16rem;
  font-weight: 900;
  color: var(--color-light);
  background-color: var(--color-blue-100);

  @media (any-hover: hover) {
    &:hover {
      background-color: #32127a;
    }
  }

  &-payment {
    width: 100%;
    font-weight: 400;
    color: var(--color-deep-blue-50);
    background-color: var(--color-button-payment);
  }

  &.--green {
    background-color: var(--color-green-100);
  }

  &-main {
    // padding: 15.8px 25px;
    padding: 10px 25px;
    min-height: 50px;
    line-height: 24px;

    &-span {
      padding-left: 55px;
      background-repeat: no-repeat;
      background-position: 23px 50%;
    }

    &.--sort-bg {
      & span {
        background-image: url(../img/sort2.svg);
      }
    }

    &.--clock-bg {
      & span {
        background-image: url(../img/clock.svg);
      }
    }
  }

  &-fade {
    font-weight: 500;
    color: var(--color-button-fade);
    background-color: transparent;
    border: 1px solid var(--color-button-fade-border);
  }

  &.--output {
    padding-left: 21px;

    & .button-main-span {
      padding-left: 33px;
      background-position: 0px 50%;
    }
  }

  &-aside {
    padding: 9px 24px 12px 47.5px;
    background-image: url(../img/aside-btn-bg.svg);
    background-repeat: no-repeat;
    background-position: 15px center;
  }

  &-center {
    margin-top: 25px;
    padding-left: 0;

    & span {
      padding-left: 55px;
      background-repeat: no-repeat;
      background-position: 23px 50%;
    }

    &.--vallet {
      & span {
        background-image: url(../img/cross.svg);
      }
    }

    &.--password {
      & span {
        background-image: url(../img/check2.svg);
      }

    }
  }
}

.button-close {
  position: absolute;
  border-radius: 100%;
  background-color: var(--color-button-danger);
  transition: background-color .3s ease-in-out;

  @media (any-hover: hover) {
    &:hover {
      background-color: var(--color-button-danger-hover);
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 54%;
    height: 2px;
    border-radius: 1px;
    background-color: var(--color-light);
  }

  &::before {
    transform: translate(-50%, -50%) rotate(45deg);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
}


@media (max-width: 627px) {

  .button {
    &-center {
      width: 100%;
    }
  }
}

.button-personal {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 32px;
  border-radius: 7px;
  background-color: var(--color-green-100);

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 17.5px;
    height: 2px;
    border-radius: 1px;
    background-color: var(--color-light);
  }

  &::before {
    transform: translate(-50%, -50%);
  }

  &::after {
    transform: translate(-50%, -50%) rotate(-90deg);
  }
}

.--big {
  font-size: 16rem;
  font-weight: 900;
  color: var(--color-personal-text-big);

  &.--regular {
    font-weight: 400;
  }
}

.--base {
  font-size: 12rem;
  color: var(--color-personal-text-big);
}

.--medium {
  font-size: 12rem;
  color: var(--color-personal-text-big);
}

.--small {
  font-size: 11rem;
  color: var(--color-deep-blue-50);
}

.--tiny {
  font-size: 10rem;
  color: var(--color-deep-blue-25);
}

.--currency {
  font-size: 10rem;
}

.--status {
  display: flex;
  align-items: center;
  min-height: 14px;
  border-radius: 10px;
  color: var(--color-black-50);
  background-color: var(--color-black-25);
}

.--icon-bg {
  padding-left: 18px;
  line-height: 1.4em;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 20px;

  &.--coin {
    background-image: url(../img/coin1.svg);
    background-position: 4% 50%;
    background-size: 18px;

    .td & {
      padding-left: 20px;
      background-position: -5% -55%;
      background-size: 19px;
    }
  }

  &.--standart-bg {
    background-size: 15px;
    background-position: 12% 100%;
  }

  &.--arrow {
    background-image: url(../img/Arrow-Left-Down.svg);
    background-position: -3% 100%;
  }

  &.--coin-ter {
    background-image: url(../img/coin-ter.svg);
  }

  &.--pdf {
    padding-left: 19px;
    background-image: url(../img/pdf.svg);
    background-size: 14px;
    background-position: 0px 2px;

    [data-theme-dark] & {
      background-image: url(../img/pdf-2.svg);
    }
  }
}

.--partners {
  padding-right: 10px;
  background-image: url(../img/chevron-right.svg);
  background-repeat: no-repeat;
  background-position: 100% 50%;
  // background-size: 20px;
}

.uppercase {
  text-transform: uppercase;
}


@media (min-width: 1440px) {

  .button-personal {
    width: 100px;
    height: 37px;
    border-radius: 14px;
  }

  .--big {
    font-size: 20rem;
  }

  .--base {
    font-size: 14rem;
  }

  .--small {
    font-size: 14rem;
  }

  .--tiny {
    font-size: 12rem;
  }

  .--icon-bg {
    background-position: 0 50%;
    background-size: 22px;
  }

  .--partners {
    padding-right: 15px;
    background-image: url(../img/chevron-right.svg);
    background-repeat: no-repeat;
    background-position: 100% 50%;
    background-size: 11px;
  }
}

.authentic {
  border-radius: 14px;
  padding: 16px 20px;
  box-shadow: var(--shadow-center-block);

  @include hover {
    box-shadow: var(--shadow-center-block-active);
  }

  & svg {
    margin-bottom: 6px;
  }

  &-title {
    margin-bottom: 8px;
    font-size: 15rem;
    font-weight: 900;
    line-height: 20px;
    color: var(--color-center-subtitle);
  }

  &-text {
    // margin-bottom: 34px;
    font-size: 14rem;
    line-height: 18px;
    letter-spacing: -0.27px;
    color: var(--color-center-bottom-text);
  }

  &-button {
    max-width: 161px;
    margin-top: 34px;
    padding: 12px 19px;
    border-radius: 10px;
    font-size: 14rem;
    font-weight: 700;
    color: var(--color-light);
    background-color: var(--color-green-100);
  }
}

.authentic.--wide.min-width-450px {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: auto auto auto;
  margin-top: 20px;

  & svg {
    margin-right: 10px;
    margin-bottom: 0;
    align-self: center;
    grid-row: 2/4;
    grid-column: 1/2;
  }

  & .authentic-title {
    grid-row: 2/3;
    grid-column: 2/3;
  }

  & .authentic-text {
    grid-row: 3/4;
    grid-column: 2/3;
  }

  & .authentic-button {
    grid-row: 4/5;
    grid-column: 1/3;
  }
}

.authentic.--wide.min-width-600px {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: auto auto auto;
  padding: 15px 30px 15px 20px;

  & svg {
    margin-right: 20px;
    align-self: center;
    grid-row: 1/3;
    grid-column: 1/2;
  }

  & .authentic-title {
    grid-row: 1/2;
    grid-column: 2/3;
  }

  & .authentic-text {
    grid-row: 2/3;
    grid-column: 2/3;
  }

  & .authentic-button {
    grid-row: 1/3;
    grid-column: 3/4;
    align-self: center;
    margin: 0;
  }
}

.output {

  &__list {
    margin-top: 2px;

    & li:not(:last-child) {
      border-bottom: 1px solid var(--color-output-item-border);
    }
  }

  &-item {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 96px;
    padding: 20px;
    padding-left: 65px;
    // padding-right: 90px;
    background-repeat: no-repeat;
    background-position: 0 center;

    &::after {
      position: absolute;
      content: "";
      width: 18px;
      height: 18px;
      right: -20px;
      top: 50%;
      transform: translate(50%, -50%);
      border-radius: 100%;
    }

    &::before {
      position: absolute;
      content: "";
      width: 2px;
      height: calc(100% - 27px);
      right: -20px;
      top: 50%;
      transform: translate(50%, 0%);
      border-radius: 1px;
    }

    &__value {
      font-size: 20rem;
      line-height: 29px;
      color: var(--color-center-subtitle);
    }

    &__descr {
      font-size: 14rem;
      line-height: 20px;
      color: var(--color-deep-blue-50);
    }

    &.--available {
      background-image: url(../img/output-available.svg);

      &::after {
        background-image: url(../img/check.svg);
      }

      &::before {
        transform: translate(50%, 16%);
        background-color: var(--color-green-100);
      }
    }

    &.--in-processing {
      background-image: url(../img/in-processing.svg);

      &::after {
        box-sizing: content-box;
        border: 6px solid var(--color-nav-border-h);
        background-color: var(--color-deep-blue-25);
      }

      &::before {
        transform: translate(50%, 25%);
        background-color: var(--color-blue-25);
      }
    }

    &.--derived {
      background-image: url(../img/output-derived.svg);

      &::after {
        background-color: var(--color-blue-25);
      }

      &::before {
        display: none;
      }
    }
  }
}
