.personal {
  display: none !important;
  background-color: var(--color-main-bg);
  display: none;

  &.visible {
    display: block;
  }


  & .center__header-top {
    justify-content: normal;
  }

  & .center__title {
    margin: 0;
    margin-right: 15px;
  }

  & .center__profile-login {
    padding: 0 5px;
    font-size: 13rem;
    line-height: 24px;
    border: 1px solid var(--color-deep-blue-25);
    border-radius: 5px;
  }

  &__balance {

    &-header {
      position: relative;
      display: flex;
      justify-content: space-between;
      margin-top: -1px;
      padding: 15px 15px 15px 63px;
      border-top-left-radius: 14px;
      border-top-right-radius: 14px;
      background-image: url(../img/coin1.svg);
      background-repeat: no-repeat;
      background-position: 12px 50%;
      background-color: var(--color-blue-100);

      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 98%;
        width: 100%;
        height: 20px;
        background-color: var(--color-blue-100);
      }
    }

    &-count {
      font-size: 10rem;
      letter-spacing: -0.03em;
      color: var(--color-light);

      & span {
        font-weight: 900;
        font-size: 18rem;
      }
    }

    &-content {
      position: relative;
      margin-top: -1px;
      border-radius: 14px;
      background-color: var(--color-center-block-bg);
      box-shadow: var(--shadow-center-block);

      &-top {
        padding: 0 15px;
      }


      &-item {
        display: flex;
        justify-content: space-between;
        column-gap: 5px;
        height: 61px;
        padding: 5px 0 5px 50px;
        background-repeat: no-repeat;
        background-position: 0 50%;

        &:not(:last-child) {
          border-bottom: 1px solid var(--color-item-border);
        }

        & .box {
          display: flex;
          flex-direction: column;
          justify-content: center;

          &.text-align-end {
            text-align: end;
          }
        }

        &.--profit {
          background-image: url(../img/profit.svg);
        }

        &.--percentage {
          background-image: url(../img/trending-up.svg);
        }

        &.--partners {
          background-image: url(../img/partners-personal.svg);
        }
      }
    }
  }
}

.look-up {
  position: relative;
  // max-width: 370px;
  padding: 14px 15px 76px 20px;
  border-radius: 14px;
  overflow: hidden;
  color: var(--color-light);
  background-image: linear-gradient(to right top, #2E2482, #4420E2);

  &__title {
    max-width: 200px;
    margin-bottom: 22px;
    font-size: 20rem;
    font-weight: 900;
    line-height: 125%;
    letter-spacing: -0.02em;
  }

  &__percentages {
    margin-right: 20px;
    vertical-align: middle;
    font-size: 30rem;
    font-weight: 900;
    line-height: 22.5px;
  }

  &__input-radio {

    &-wrap {
      display: inline-flex;
      justify-content: space-evenly;
      align-items: center;
      height: 24px;
      width: 200px;
      // padding: 5px;
      border-radius: 14px;
      // border: 1px solid var(--color-blue-100);
      font-size: 12rem;
      color: var(--color-deep-blue-50);
      background-color: var(--color-light);
    }

    &:checked+&-label {
      color: var(--color-light);
      background-color: var(--color-blue-100);
    }

    &-label {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 104%;
      width: 38%;
      border-radius: 10px;
    }
  }

  &__descr {
    margin-top: 15px;
    letter-spacing: -0.02em;
  }

  &__input-wrap {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 60px;
    border-radius: 14px;

    &-input {
      width: 100%;
      height: 100%;
      padding: 0 50px 0 20px;
      border-radius: 14px;
      border: 2px solid #4318FF;

      &::placeholder {
        font-size: 16rem;
        color: var(--color-deep-blue-50);
      }
    }

    &-button {
      position: absolute;
      width: 33px;
      height: 33px;
      right: 13px;
      top: 50%;
      transform: translateY(-50%);
      border-radius: 14px;
      background-color: var(--color-green-100);

      &::before {
        position: absolute;
        content: "";
        left: 50%;
        top: 50%;
        transform: translate(-50%, -79%) rotate(-45deg);
        width: 12px;
        height: 8px;
        border-bottom: 2px solid var(--color-light);
        border-left: 2px solid var(--color-light);
      }

      &::after {
        position: absolute;
        content: "USDT";
        font-size: 12rem;
        font-weight: 900;
        color: var(--color-deep-blue-25);
        left: -130%;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  &__burn {
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100px;
    height: 59px;
    border-radius: 14px;
    background-image: linear-gradient(rgb(4%, 65%, 43%, 1) -7%, rgb(4%, 65%, 43%, 0) 95%),
      linear-gradient(rgb(17%, 21%, 45%, 0), rgb(17%, 21%, 45%, 0.2), rgb(17%, 21%, 45%, 0));

      // &::after {
      //   content: "";
      //   position: absolute;
      //   width: 20px;
      //   height: 20px;
      //   right: 10px;
      //   top: 10px;
      //   background-image: url(../img/arrow-right-up.svg);
      // }

    &-percentages {
      font-size: 20rem;
      font-weight: 600;
      text-decoration: underline;
      color: var(--color-light);

      &::before {
        content: url(../img/burn.svg);
        display: inline-block;
        // width: 20px;
        // height: 16px;
        vertical-align: middle;
      }
    }
  }
}

.invest {
  // display: none !important;
  --column-gap: 5px;
  --grid-template-colulmns: repeat(7, calc(14.28571428571429% - calc((var(--column-gap) * 6) / 7)));
  margin-top: 20px;
  padding-top: 14px;
  // padding-bottom: 20px;
  border-radius: 14px;
  background-color: var(--color-center-block-bg-invest);
  box-shadow: var(--shadow-center-block);

  & .center__form-top {
    padding: 0 15px;
    margin-bottom: 24px;
  }

  &__table {
    width: 100%;
    padding: 0 20px;

    .inners-hidden &-header {
      display: grid;
      grid-template-columns: var(--grid-template-colulmns);
      column-gap: var(--column-gap);
      margin-bottom: 20px;

      & .th {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: 8rem;
        font-weight: 400;
        text-align: left;
        // background-color: yellow;

        & button {
          display: inline-block;
          padding-right: 15px;
          text-transform: uppercase;
          text-align: left;

          & span {
            position: relative;
            color: var(--color-center-subtitle);

            & svg {
              position: absolute;
              right: -15px;
              top: 50%;
              transform: translateY(-30%);
              width: 13px;
              height: 11px;
              fill: currentColor;
              fill: var(--color-deep-blue-50);
            }
          }
        }
      }

      & > *:nth-child(7n) {
        justify-content: end;

        .output & {
          justify-content: flex-start;
        }
      }
    }

    &-header {
      display: none;
      // padding: 0 6px;
    }

    .inners-hidden &-body {
      // margin: 0 -15px;
      padding: 0 15px;

      & .td {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 59px;

        &.--column {
          flex-direction: column;
          justify-content: center;
          align-items: flex-start;
          row-gap: 5px;
        }

        &-caption {
          display: none;
        }
      }
    }

    &-body {
      margin: 0;
      // background-color: var(--color-light);
      background-color: transparent;
      // box-shadow: var(--shadow-center-block-table);

      & .td {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: auto;
        padding-top: 10px;
        padding-bottom: 10px;
        text-align: center;
        color: var(--color-personal-text-big);
        // background-color: yellowgreen;

        &:not(:last-child) {
          padding-right: 5px;
        }

        &-caption {
          display: inline;
          margin-bottom: 5px;
          text-align: center;
          color: var(--color-deep-blue-50);
          font-size: 10rem;
        }
      }
    }

    .inners-hidden &-body {
      margin: 0 -15px;
      padding: 0 15px;
      border-radius: 14px;
      border: 1px solid var(--color-item-border);
      background-color: var(--color-center-block-bg);
      box-shadow: var(--shadow-center-block-table);
    }

    .inners-hidden &-row {
      display: grid;
      grid-template-columns: var(--grid-template-colulmns);
      column-gap: var(--column-gap);
      margin: 0;
      // margin: 0 -15px;
      padding: 0;
      // padding: 0 15px;
      border-radius: 0;
      border: none;
      box-shadow: none;

      & > *:last-child {
        justify-content: end;

        .output & {
         justify-content: normal;

         & .--status {
          padding: 0 7px;
         }
        }
      }

      & .inner {
        display: none;
      }

      &:not(:last-child) {
        margin-bottom: 0;
        border-bottom: 1px solid var(--color-item-border);
      }
    }

    &-row {
      column-gap: normal;
      margin: 0 -15px;
      padding: 0 15px;
      border-radius: 14px;
      border: 1px solid var(--color-item-border);
      background-color: var(--color-center-block-bg);
      box-shadow: var(--shadow-center-block-table);

      & .inner {
        display: flex;
        justify-content: space-between;

        &:not(:last-child) {
          border-bottom: 1px solid var(--color-item-border);
        }
      }

      &:not(:last-child) {
        // border-bottom: none;
        margin-bottom: 10px;
      }

      & > *:nth-child(7n) {
        justify-content: inherit;
      }
    }
  }

  &__pagination {
    padding: 0 15px;
    color: var(--color-deep-blue-50);

    &-header {
      padding: 8px 0;
    }

    &-range {
      width: 75px;
      height: 3px;
      border-radius: 10px;
      background-color: var(--color-deep-blue-25);

      .inners-hidden & {
        width: 91px;
        height: 4px;
        border-radius: 20px;
      }
    }

    &-body {
      display: none;

      .inners-hidden & {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 0;
        font-weight: 700;
      }
    }

    &-left {
      display: flex;
      align-items: center;
      gap: 5px;
      font-size: 12rem;
    }

    &-right {
      display: flex;
      align-items: center;
      gap: 5px;
      font-size: 14rem;
    }

    &-button {
      min-width: 36px;
      height: 28px;
      padding: 0 12px;
      border-radius: 14px;
      color: inherit;

      @media (any-hover: hover) {
        &:hover:not(.invest__pagination-button--active) {
          color: var(--color-deep-blue-100);
        }
      }

      &--active {
        color: var(--color-light);
        background-color: var(--color-deep-blue-100);
      }

      &-menu {
        display: flex;
        & svg {
          width: 14px;
          height: 14px;
          fill: var(--color-deep-blue-50);
        }
      }
    }

    &-ellipsis {
      display: flex;
      gap: 2px;

      & i {
        width: 2px;
        height: 2px;
        border-radius: 100%;
        background-color: currentColor;
      }
    }
  }
}

@media (min-width: 1440px) {

  .personal {
    margin-top: 3px;

    &__balance {

      &-header {
        align-items: center;
        padding: 26px;
        padding-left: 89px;
        padding-right: 20px;
        border-radius: 14px;
        background-size: 67px;

        &::after {
          display: none;
        }
      }

      &-count {
        font-size: 12rem;

        & span {
          font-size: 20rem;
        }
      }

      &-content {
        display: flex;
        gap: 20px;
        padding: 20px;

        &-top {
          margin: -20px 0;
          padding: 0;
          flex-grow: 1;
        }

        &-item {
          height: 90px;
          padding: 5px 0 5px 64px;
          background-size: 45px;
        }
      }
    }
  }

  .look-up {
    width: 370px;
    padding: 15px 15px 84px 20px;

    &__percentages {
      font-size: 34rem;
    }

    &__input-radio {

      &-wrap {
        height: 28px;
      }
    }

    &__descr {
      margin-top: 12px;
    }
  }
}

