.center-bottom {
  display: none;
  margin-top: 20px;

  &.--active {
    display: block;
  }

  &__text {
    margin-top: 2px;
    margin-bottom: 18px;

    line-height: 125%;
    font-size: 16rem;
    color: var(--color-center-bottom-text);
  }

  &__label {
    margin-bottom: 12px;

    @media (any-hover: hover) {
      &:hover input {
        background-color: #f7f7f7;


        [data-theme-dark] & {
          background-color: var(--color-deep-blue-50);
          color: var(--color-dark-deep-blue-100);
        }
      }

    }


  }
}


@media (max-width: 627px) {
  .center-bottom {
    margin-top: 9px;

    &__subtitle {
      margin-bottom: 4px;
    }

    &__text {
      font-size: 14rem;
      line-height: 143%;
    }

    &__button {
      width: 100%;
    }
  }
}
